


















var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]

export default {
	name: 'imgbox',
	props: {
		max: {
			type: Number,
			default: 5
		},
		title: {
			type: String,
			default: '图片'
		}
	},
	data() {
		return {
			imageList: [],
		}
	},
	methods: {
		chooseImage: async function() {

			uni.chooseImage({
				sourceType: ['camera', 'album'],
				sizeType: ['compressed', 'original'],
				count: this.max - this.imageList.length,
				success: (res) => {
					console.log(res);
					this.imageList = this.imageList.concat(res.tempFilePaths);
				}
			})
		},
		previewImage: function(src) {
			uni.previewImage({
				current: src,
				urls: this.imageList
			})
		},
		getImageList(){
			return this.imageList;
		}
	}
}
