var api = {
	BASE_URL: 'http://192.168.1.129:8080',
	WXLOGIN: '/eidpws/wxTrace/wxLogin/wxLogin',
	GETMENU:'/eidpws/wxTrace/wxLogin/getMenu',
	FINDNORMALCUSTOMERSAFETYTASKDANGERS:'/eidpws/wxTrace/wxSafety/findNormalCustomerSafetyTaskDangers',
	FINDAREA:'/eidpws/wxTrace/wxSafety/findArea',
	FINDSTREET:'/eidpws/wxTrace/wxSafety/findStreet',
	FINDCOMMUNITYREPOSITORY:'/eidpws/wxTrace/wxSafety/findCommunityRepository',
}

export default function(option) {

	if (!api[option.url]) {
		uni.showToast({
			icon: 'none',
			duration: 2000,
			title: '请求路径不在接口列表中'
		});
		
		return;
	}

	if (option.showLoading) {
		uni.showLoading({
			mask: true,
			title: option.loadingText
		})
	}

	let url = api.BASE_URL + api[option.url];

	function fail(e) {
		console.log('fail:' + url)
		console.log(e);
		if (option.fail) {
			option.fail(e)
		} else {
			uni.showToast({
				icon: 'none',
				duration: 2000,
				title: e.errMsg || '网络请求失败'
			})
		}
	}

	return uni.request({
		url: url,
		data: option.data || {},
		method: option.method || 'GET',
		success: (res) => {
			if (res.statusCode === 200 && res.data.status == true) {
				console.log('success:' + url)
				console.log(res);
				option.success && option.success(res.data.data);
			} else {
				res.errMsg = res.data.msg;
				fail(res)
			}
		},
		fail,
		complete() {
			if (option.showLoading) {
				uni.hideLoading();
			}

			option.complete && option.complete();
		}
	});
}
